.alter{
    margin-bottom: 3rem;
    margin-top: 7rem;
    height: 23.5rem;
    overflow: scroll;
    h3{
        padding-left: 20px;
        margin-top: 20px;
        border: none;
        color: blue;
        text-align: center;
        }
    h4{
        padding-left: 20px;
        margin-top: 20px;
        border: none;
        text-align: center;
    }
    .card{
        border:1px solid #333;
        width:17rem;
        margin:1rem auto;  
        padding:0.5rem; 
    }
    button{
        z-index: 200;
        position: fixed;
        bottom: 0;
        border:none;
        width:100vw;
        height: 2.25rem;
        font-size: 0.9rem;
        left:50%;
        transform: translateX(-50%);
        background-color: #108ee9; 
        color:#fff;
    }
}