    .card {
        width: 100%;
        padding: 0.2rem;
        background-color: #fff;
        box-shadow: 0px 0px 2px antiquewhite;
        margin-bottom: 0.5rem;
        .box {
            display: flex;
            justify-content: space-around;
            //margin-top: 2.5rem;
            padding: 0.3rem;

            img {
                width: 6rem;
                height: 6rem;
            }

            .name-price {
                display: flex;
                flex-direction: column;
                justify-content: center;
                height: 6rem;

                .bookname {
                    font-size: 0.8rem;
                    padding: 0.5rem;
                }

                .price {
                    font-size: 1.5rem;
                    color: red;
                    text-align: center;
                }
            }
        }
        .num {
            font-weight: bold;
            font-size: 14px;
            text-align: right;
            padding-right: 1rem;
        }


    }

    .add-time {
        text-align: left;
        font-size: 0.8rem;
        color: #000;
        font-weight: bold;
        line-height: 2rem;
    }

    .goods-detail,
    .check-addr {
        text-align: center;
        font-size: 1rem;
        font-weight: bold;
    }

    .goods-detail-box {
        box-shadow: 0 0 2px #ddd;
        background-color: mintcream;

        .img-name-price {
            display: flex;
            height: 5rem;

            .img {
                width: 5rem;
                height: 5rem;
            }

            p {
                margin: 0;
            }

            .name-price {
                width: 50%;
                height: 5rem;
                display: flex;
                flex-direction: column;
                justify-content: space-around;

                .bookname,
                .price {
                    text-align: center;
                    height: 1.5rem;
                    font-size: 1rem;
                    line-height: 1.5rem;
                    font-weight: bold;
                }

                .price {
                    font-size: 1.5rem;
                    color: yellow;
                    font-style: italic;
                }

            }
        }

        .intro {
            width: 15rem;
            height: 3rem;
            text-align: center;
            overflow-x: hidden;
            overflow-y: scroll;
            word-wrap: break-word;
            word-break: break-all;
        }
    }

    .addr-box {
        width: 100%;
        padding: 0.2rem;
        box-shadow: 0 0 2px #ddd;
        background-color: #fff;

        .addr-descripe {
            text-align: center;
            font-size: 0.75rem;
        }
    }

    .total-price {
        font-size: 1rem;
        color: red;
        font-weight: bold;
        line-height: 2rem;
    }

    footer {
        width: 100vw;
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 200;

        button {
            width: 50%;
            background-color: brown;
            border: none;
            text-align: center;
            height: 2.25rem;
            font-size: 0.9rem;
            color: #fff;
        }

        & button:nth-child(2) {
            background-color: coral;
        }
    }
}