.picker-list .am-list-item .am-list-line .am-list-extra {
    flex-basis: initial;
  }
  
  .test {
    position: relative;
    border-bottom: 0;
  }
  
  .test:after {
    content: '';
    position: absolute;
    background-color: #ddd;
    display: block;
    z-index: 1;
    top: auto;
    right: auto;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1PX;
    -webkit-transform-origin: 50% 100%;
    -ms-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
    -webkit-transform: scaleY(0.5);
    -ms-transform: scaleY(0.5);
    transform: scaleY(0.5);
  }
  .mine {
    padding: 0.4rem;
    margin-top:45px;
    min-height: 100vh;
      header {
          display: flex;
          justify-content: space-between;
          .user-pic {
              width: 3rem;
              height: 3rem;
              border-radius:  50%;
              background:url(../images/head.jpg) no-repeat;
              background-size: 3rem 3rem;
              background-position: center;
          }
          .nickname {
              width: 3rem;
              height: 3rem;
              text-align: center;
              line-height: 3rem;
              font-size: 1rem;
              color: blue;
              font-style: italic;
              font-weight: bold;
          }
          margin-bottom: 1rem;  
      }
      .modify-pass-head, .history-orders-head {
        height: 2rem;
        margin-bottom: 1rem; 
        text-align: left;
        line-height: 2rem;
        position: relative;
        padding-left:1rem;
        &::after {
            content:"";
            width: 0.5rem;
            height:0.5rem;
            border-top:1px solid #999;
            border-right: 1px solid #999;
            transform:rotate(45deg);
            position: absolute;
            top:0.75rem;
            right: 0.9rem;
        }
      }
      .modify-pass-head {
        background-color: rosybrown;
        color: #fff;
      }
      .history-orders-head {
        background-color: royalblue;
        color: #fff;
      }
      .select-region {
        height:2rem;
        background-color: olivedrab;
        .am-list-content,.am-list-extra{
          color:#fff;
          font-size: 0.7rem;
        }
      }
      .addr {
          height: 7rem;
          border: 1px solid #333;
          display:flex;
          justify-content: center;
          position: relative;
          margin-top:1rem;
          textarea {
            padding: 0.5rem 0.5rem 0;
            outline: none;
            border: none;
            height:5rem;
          }
          .icon,.save {
            display:inline-block;
            width:2rem;
            height:1rem;
            text-align: center;
            color:blue;
            text-decoration: underline;
            position: absolute;
            bottom: 0.8rem;
            right: 3rem;
          }
          .icon{
            right:1rem;
          }
      }
  }