.chart{
    background-color: #eee;
    padding: 1rem;
    margin-bottom: 2.5rem;
    margin-top:45px;
    min-height: 100vh;
    .card{
        padding:0.2rem;
        background-color: #fff;
        width:100%;
        height:6rem;
        box-shadow: 0px 0px 2px antiquewhite;
        position: relative;
        .img-name-price{
            display:flex;
            height:4rem;
            img{
                width:7rem;
                height:4rem;
            }
             .bookname-price{
                 flex:1;
                 margin-bottom: 1rem;
                 height:4rem;
                 .bookname{
                     text-align: center;
                     font-size: 0.75rem;
                     color:#000;
                     //line-height: 2rem;
                 }
                 .price{
                     text-align: center;
                     font-size: 1.5rem;
                     color:red;
                     text-align: center;
                     line-height: 2rem;
                 }
             }
        }
        .num{
            position: absolute;
            right: 0.8rem;
            bottom: 0.68rem;
        }
    }
    .add-time{
        text-align: left;
        font-size: 0.8rem;
        color:#000;
        font-weight: bold;
        line-height: 2rem;
    }
    // button{
    //     position: fixed;
    //     bottom: 0;
    //     width:calc(100% - 2rem);
    //     height:3rem;
    //     border:none;
    //     background-color: chocolate;
    // }
    footer{
        width:100vw;
        position: fixed;
        bottom:0;
        left:0;
        button {
            width:50%;
            background-color: brown;
            border:none;
            text-align: center;
            height:2.5rem;
            height: 2.25rem;
            font-size: 0.9rem;
            color:#fff;
        }
        & button:nth-child(2){
            background-color: coral;
        }
    }
}