.sys{
    margin-top:45px;
    .btns{
        display: flex;
        justify-content: space-around;
        position: fixed;
        height: 4rem;
        top: 2rem;
        left: 50%;
        transform: translateX(-50%);
        z-index: 200;
        width: 100vw;
        border-bottom: 1px double burlywood;

        p{
            width:3rem;
            height:2rem;
            text-align: center;
            line-height: 2rem;
            font-size: 1rem;
        }
        .add-btn{
           background-color: antiquewhite;
        }
        .del-btn{
            background-color: bisque;
        }
        .alter-btn{
            background-color: brown;
        }
        .find-btn{
            background-color: chocolate;
        }
    }
}