.books {
    border-bottom: 1px solid red;
    margin-bottom: 0.5rem;
    display:flex;
    flex-wrap: wrap;
    margin-bottom: 2rem;
    .card{
        width: 30%;
        height: 6rem;
        margin: 0 0.25rem 0.5rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        img{
            width:100%;
            height:3rem;
        }
        p{
            font-size: 0.75rem;
            text-align: center;
            margin:0.25rem;
        }
    }
    .people-like {
        font-size: 1rem;
        color: blue;
    }
    .book-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width:100%;
        .book {
            display: block;
            width: 5rem;
            height: 4rem;
        }
    }
    
}