.history-orders{
    margin-top:45px;
    overflow: hidden;
    .card {
        width: 90%;
        padding: 0.2rem;
        //background-color: #fff;
        box-shadow: 0px 0px 2px antiquewhite;
        // margin-bottom: 0.5rem;
        margin: 1rem auto 0;
        background-color: rgba(255, 85, 0, 0.18823529411764706);
        border-radius: 12px;
        .box {
            display: flex;
            justify-content: space-around;
            //margin-top: 2.5rem;
            padding: 0.3rem;

            img {
                width: 6rem;
                height: 6rem;
            }
            .add-time {
                height: 1rem;
            }
            .name-price {
                display: flex;
                flex-direction: column;
                justify-content: center;
                height: 6rem;

                .bookname {
                    font-size: 0.8rem;
                    padding: 0.5rem;
                }

                .price {
                    font-size: 1.5rem;
                    color: red;
                    text-align: center;
                }
            }
        }
        .num {
            font-weight: bold;
            font-size: 14px;
            text-align: right;
            padding-right: 1rem;
        }


    }
    .add-time{
        text-align: left;
        font-size: 0.8rem;
        color:#000;
        font-weight: bold;
        line-height: 2rem;
    }
}