.main {
    padding: 0.4rem;
    margin-top:45px;
    header {
        display: flex;
        justify-content: space-between;
        .icon {
            width: 2rem;
            height: 2rem;
            border-radius: 50%;
            background:url(../images/icon.jpg) no-repeat;
            background-size: 2rem 2rem;
            background-position: center;
        }
        .search-box {
            flex: 1;
            height: 2rem;
            border: 1px solid #eaeaea;
            border-radius: 2px;
            display: flex;
            align-items: center;
            margin-left: 0.85rem;
            input {
                width: calc(100% - 0.5rem);
                height: 1.5rem;
                border: none;
                padding-left: 0.5rem;
                outline: none;
            }
            .searchIcon {
                background: url(../images/search.jpg) no-repeat;
                background-size: 4.5rem 1.5rem;
                width: 1.5rem;
                height: 1.5rem;
                background-position: center;
            }
        }
        .login-register-mine {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 5rem;
            height: 2rem;
        }
        .exitLogin {
            font-size: 0.8rem;
            text-decoration: underline;
            color:blue;
            margin:0;
            width: 2rem;
            height: 2rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
    .my-swiper {
        height: 6rem;
        margin-top: 0.8rem;
    }
    .segments{
        width:100%;
        height: 2rem;
        display: flex;
        align-items: center;
        margin-bottom: 1rem;
        .active {
            border-bottom: 1px solid blue;
        } 
    .segment {
        width: 20%;
        height: 100%;
        text-align: center;
    }  
    }
    .content {
        width: 100%;
        height: auto;
    }
    footer {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        height: 2rem;
        background-color: #fff;
        .chart-bottom {
            width: 3rem;
            height: 3rem;
            margin: 0 auto;
            background-image:url(../images/chart.jpg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: 3rem 3rem;
            position: relative;
            bottom: 1rem;
            // &::after {
            //     content: "";
            //     display: block;
            //     width: 4.5rem;
            //     height: 4.5rem;
            //     border-radius: 50%;
            //     border: 1px solid rgb(0,160,250);
            //     position: relative;
            //     left: -0.7rem;
            // }
        }
        .goods-num{
            position: relative;
            left: 2rem;
            // top: -0.4rem;
            width:1rem;
            height:1rem;
            border-radius: 50%;
            background: red;
            color:#fff;
            font-size: 0.6rem;
            text-align: center;
            line-height: 1rem;
        }
    }
}

