.search-result{
    display:flex;
    flex-wrap: wrap;
    min-height: 100vh;
    margin:1rem 0 2rem 0;
    .card{
        width: 30%;
        height: 9rem;
        margin: 0 0.25rem 0.5rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        img{
            width:100%;
            height:5rem;
        }
        p{
            font-size: 0.75rem;
            text-align: center;
            margin:0.25rem;
        }
        .novel-price{
            color:red;
        }
    }
    .empty{
        width:100%;
        display:flex;
        justify-content: center;
        align-items: center;
        font-size: 0.8rem;
        color:#666;
    }
    .find-res{
        width:5rem;
        height:8rem;
        padding:1rem;
        padding-bottom: 0.25rem;
        align-items: center;
        background-color: bisque;
        margin:1rem auto;
        .img-name-price{
            display:flex;
            .img{
                width:3rem;
                height:3rem;
            }
            p{
                margin:0;
            }
            .name-price{
                width:50%;
                display:flex;
                flex-direction: column;
                justify-content: space-around;
                .bookname,.price{
                    text-align: center;
                    font-size: 0.7rem;
                    font-weight: bold;
                }
                .price{
                    color:red;
                    font-style: italic;
                }
               
            }  
        }
        .intro{
            background-color: #fff;
            height:3rem;
            text-align: center;
           overflow-x:hidden;
           overflow-y: scroll;
           word-wrap: break-word;
           word-break: break-all;
        } 
    }
}