.book-details{
    margin-top:45px;
    .img-price{
        display:flex;
        justify-content: space-around;
        margin-top: 2.5rem;
        padding:0.3rem;
         img{
            width:6rem;
            height:6rem;
        }
        .name-price{
            display: flex;
            flex-direction: column;
            justify-content: center;
            height:6rem;
            .bookname{
                font-size: 0.8rem;
                padding: 0.5rem;
            }
            .price{
                font-size: 1.5rem;
                color:red;
                text-align: center;
            }
        }
        
    }
    .introduce{
        font-size: 1rem;  
        padding:2rem;  
        margin-bottom: 2.25rem;  
    }
    footer{
        position: fixed;
        bottom:0;
        left:0;
        right:0;
        button{
            width:50%;
            height: 2.25rem;
            font-size: 0.9rem;
            border:none;
            color:#fff;
        }
        .add-chart {
            background-color: brown;
        }
        .buy-immediate{
            background-color: chocolate;
        }
    }
}