.novel{
    display:flex;
    flex-wrap: wrap;
    margin-bottom: 2rem;
    .card{
        width: 30%;
        height: 9rem;
        margin: 0 0.25rem 0.5rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        img{
            width:100%;
            height:5rem;
        }
        p{
            font-size: 0.75rem;
            text-align: center;
            margin:0.25rem;
        }
        .novel-price{
            color:red;
        }
    }
}