.find{
    margin-bottom: 2rem;
    margin-top: 7rem;
    height: 23.5rem;
    overflow: scroll;
    h3{
        padding-left: 20px;
        margin-top: 20px;
        border: none;
        color: blue;
        text-align: center;
    }
    h4{
        padding-left: 20px;
        margin-top: 20px;
        border: none;
        text-align: center;
    }
    .find-res{
        width:15rem;
        height:9rem;
        padding:1rem;
        padding-bottom: 0.25rem;
        align-items: center;
        background-color: bisque;
        margin:1rem auto;
        .img-name-price{
            display:flex;
            height:5rem;
            .img{
                width:5rem;
                height:5rem;
            }
            p{
                margin:0;
            }
            .name-price{
                width:50%;
                height:5rem;
                display:flex;
                flex-direction: column;
                justify-content: space-around;
                .bookname,.price{
                    text-align: center;
                    height:1.5rem;
                    font-size: 1rem;
                    line-height: 1.5rem;                    
                    font-weight: bold;
                }
                .price{
                    font-size: 1.5rem;
                    color:red;
                    font-style: italic;
                }
               
            }  
        }
        .intro{
            background-color: #fff;
            width:15rem;
            height:3rem;
            text-align: center;
           overflow-x:hidden;
           overflow-y: scroll;
           word-wrap: break-word;
           word-break: break-all;
        } 
    }
    button{
        z-index: 200;
        position: fixed;
        bottom: 0;
        border:none;
        width:100vw;
        height: 2.25rem;
        font-size: 0.9rem;
        left:50%;
        transform: translateX(-50%);
        background-color: #108ee9; 
        color:#fff;
    }
}